import { Component, HostListener, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {



  ngOnInit(): void {
    this.sticky = (this.navbar[0] as HTMLElement).offsetTop;
  }
  navbar: HTMLCollectionOf<Element> = document.getElementsByClassName('navbar');
  sticky = null;
  title = 'mysite';

  @HostListener('window:scroll') onScroll() {
    if (this.navbar.length < 2) {
      if (window.pageYOffset >= this.sticky) {
        this.navbar[0].classList.add("sticky")
      } else {
        this.navbar[0].classList.remove("sticky");
      }
    }
  }

}
